ze: 16px;\r\n  font-weight:300;\r\n  color:#9c9c9c;\r\n}\r\n/* Testimonial */\r\n.testimonial q {\r\n  font-family:  'Neue Haas Grotesk Dark';\r\n  font-size: 17px;\r\n  font-weight:300;\r\n}\r\n.testimonial.classic q, .testimonial.classic footer {\r\n  color:#111;\r\n}\r\n/* Standard Headings h1-h6 */\r\nh1 {\r\n  font-family:  'Neue Haas Grotesk Dark';\r\n  font-size: 40px;\r\n  font-weight:300;\r\n  color:#111;\r\n}\r\nh2 {\r\n  font-family:  'Neue Haas Grotesk Dark';\r\n  font-size: 34px;\r\n  font-weight:300;\r\n  color:#111;\r\n}\r\nh3 {\r\n  font-family:  'Neue Haas Grotesk Dark';\r\n  font-size: 30px;\r\n  font-weight:700;\r\n  color:#111;\r\n}\r\nh4 {\r\n  font-family:  'Neue Haas Grotesk Dark';\r\n  font-size: 18px;\r\n  font-weight:400;\r\n  color:#111;\r\n}\r\nh5 {\r\n  font-family:  'Neue Haas Grotesk Light';\r\n  font-size: 16px;\r\n  font-weight:400;\r\n  color:#111;\r\n}\r\nh6 {\r\n  font-family:  'Neue Haas Grotesk Light';\r\n  font-size: 14px;\r\n  font-weight:400;\r\n  color:#111;\r\n}\r\n/*------------------------------------------------------------------------------------------*/\r\n/* 6. Buttons */\r\n/*------------------------------------------------------------------------------------------*/\r\n/* ----------Default Buttons---------- */\r\n/* Button Text */\r\n.button, input[type=\"submit\"]  {\r\n  font-family:  'Neue Haas Grotesk Dark';\r\n  font-size:14px;\r\n  font-weight:bold;\r\n  color:#111;\r\n}\r\n/* Button Color */\r\n.button, input[type=\"submit\"] {\r\n  border-color:#111;\r\n}\r\n/* Button Hover Color */\r\n.button:hover,  input[type=\"submit\"]:hover {\r\n  border-color:#ff0045;\r\n  color:#ff0045;\r\n}\r\n/* ----------Banner Buttons---------- */\r\n/* Button Text */\r\n#banner .button {\r\n  font-family: 'Neue Haas Grotesk Dark';\r\n  letter-spacing: 0.5px;\r\n  font-size: 16px;\r\n}\r\n/* Button Color */\r\n#banner .button {\r\n  border-color:#111;\r\n}\r\n/* Button Hover Color */\r\n#banner .button:hover {\r\n  color:#ff0045;\r\n  border-color:#ff0045;\r\n}\r\n/*------------------------------------------------------------------------------------------*/\r\n/* 7. Footer */\r\n/*------------------------------------------------------------------------------------------*/\r\n#landing-footer, #landing-footer p, #landing-footer a {\r\n  font-family:  'Neue Haas Grotesk Light';\r\n  font-size: 20px;\r\n  font-weight:normal;\r\n  color:#999;\r\n}\r\n/* Footer Icon Color */\r\n#landing-footer i {\r\n  color:#999;\r\n}\r\n@media (max-width: 480px) {\r\n\t\t#banner{\r\n\t\t  background: none;\r\n\t\t}\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]